/// <reference path="../../build/node_modules/@types/jquery/index.d.ts" />
/// <reference path="../../lib/alumican.d.ts" />

namespace test {

	export class Main {
		constructor() {
			(<any>window).onMenuClick = function(path) {
				sceneManager.goto(path);
			};

			// --------------------------------------------------
			// Build scene
			const sceneManager = new scn.SceneManager("homepage");

			const rootScene = sceneManager.getRoot();

			trace(124);
			trace(jQuery(".page-link"));
			jQuery(".page-link").each((index:number, element:HTMLElement):void => {
				trace(element);
			});

			const aboutScene = new scn.Scene("about");
			rootScene.addChild(aboutScene);

			const galleryScene = new scn.Scene("gallery");
			rootScene.addChild(galleryScene);

			const webScene = new scn.Scene("web");
			galleryScene.addChild(webScene);
			for (let i = 0; i < 2; ++i) {
				const contentScene = new scn.Scene("web-" + (i + 1));
				webScene.addChild(contentScene);
			}

			const photoScene = new scn.Scene("photo");
			galleryScene.addChild(photoScene);
			for (let i = 0; i < 4; ++i) {
				const contentScene = new scn.Scene("photo-" + (i + 1));
				photoScene.addChild(contentScene);
			}

			const movieScene = new scn.Scene("movie");
			galleryScene.addChild(movieScene);
			for (let i = 0; i < 1; ++i) {
				const contentScene = new scn.Scene("movie-" + (i + 1));
				movieScene.addChild(contentScene);
			}

			const contactScene = new scn.Scene("contact");
			rootScene.addChild(contactScene);

			contactScene.onArrive = function() {
				return new cmd.Serial(
					new cmd.Wait(1000),
					function() {
						document.getElementById("page-title").textContent = "Contact";
					}
				);
			};

			// --------------------------------------------------
			// Start
			sceneManager.start();

			//sceneManager.goto("gallery/web/");
			//console.log(sceneManager.resolvePath("/"));
			//console.log(sceneManager.resolvePath("/aaa/bbb/"));
			//console.log(sceneManager.resolvePath("aaa/bbb/ccc"));
			//console.log(sceneManager.resolvePath("./aaa/bbb"));
			//console.log(sceneManager.resolvePath("aaa/bbb/../ccc"));
			//console.log(sceneManager.resolvePath("./aaa/./bbb/.././ccc"));

			//console.log(sceneManager.getSceneByPath("/").getName());
			//console.log(sceneManager.getSceneByPath("/gallery").getName());
			//console.log(sceneManager.getSceneByPath("/gallery/photo").getName());
			//console.log(sceneManager.getSceneByPath("/gallery/photo/photo-2").getName());
		}
	}
}

new test.Main();